import mimes from "@constants/mime.json"
import Mdloader from "@components/Mdloader";
// @ts-ignore
import { SearchInput, Table } from "evergreen-ui";
import { useState, useEffect } from "react";
import { createFuzzyList } from "@utils/fuzzyScore";

const lists = createFuzzyList(mimes)

export default function () {
  const [query, setQuery] = useState("");
  const [data, setData] = useState(lists);
  useEffect(() => {
    let sorted = [];
    if (query) {
      sorted = lists.filter(function (item) {
        return (item._cachedScore = item.score(query)) >= 0.5;
      }).sort(function (a, b) {
        var as = a._cachedScore;
        var bs = b._cachedScore;
        return as > bs ? -1 : as == bs && a.i < b.i ? -1 : 1;
      })
    } else {
      sorted = lists;
    }
    setData(sorted);
  }, [query]);
  return (
    <>
      <SearchInput
        placeholder="Typing something"
        onChange={e => {
          setQuery(e.target.value);
        }}
        value={query}
        width={"600px"}
        marginX="auto"
        height={40}
        marginBottom={10}
      />
      <Table>
        <Table.Head>
          <Table.TextHeaderCell>
              Extension
          </Table.TextHeaderCell>
          <Table.TextHeaderCell>
            Content-Type(Mime-Type)
          </Table.TextHeaderCell>
        </Table.Head>
        <Table.Body >
          {data.map((item, ix) => (
            <Table.Row key={item[0] + ix } isSelectable onSelect={() => alert(item.toString())}>
              <Table.TextCell>.{item[0]}</Table.TextCell>
              <Table.TextCell>{item[1]}</Table.TextCell>
            </Table.Row>
          ))}
        </Table.Body>
      </Table>
      <style jsx>{`
        table.entitles {
          font: 0.8em Monaco;
          display: block;
          text-align: center;
        }
      `}</style>
    </>
  );
}

